* {


    box-sizing: border-box;
  
    padding: 0;
  
    margin: 0;
  
    font-family: "Josefin Sans", "Arial Narrow", Arial, sans-serif;
  
  }
  
  
#appbar {
    display: flex !important;
    justify-content: space-evenly;
    align-items: center;
    background: rgb(245, 34, 34);
    color: white;
  }
#appbar .items {
  color: white;
  font-weight: bolder;
  text-decoration: none;
  margin: 5px 0;
  border-radius: 4px;
 padding: 10px 15px;
}

#appbar .items:hover {
  background: rgb(233, 7, 7);

}
#search {
  width: 50%;
  padding: 10px 5px ;
  display: block;
  border: 3px solid rgb(36, 216, 240);
  border-radius: 4px;
  font-size: 20px;
  outline: none;
  margin: 20px auto;
}
#sresult {
  width: 95%;
  margin: 30px auto;
  display: flex;
  justify-content: center;
  align-items: center;
}